h1,
h2,
h3,
h4,
h5,
h6,
p {
  margin: 0 0 0.75em;
}

h1:last-child,
h2:last-child,
h3:last-child,
h4:last-child,
h5:last-child,
h6:last-child,
p:last-child {
  margin: 0;
}

h1 {
  font-size: 2rem;
  font-weight: $font-light;
  line-height: 1.15;
}

h2 {
  font-size: 1.2rem;
  //text-transform: uppercase;
  letter-spacing: 1.5px;
  line-height: 1;
}
